<template>
  <div>
    <h1>{{ id ? "编辑" : "添加" }}分类</h1>
    <el-form ref="editForm"
             :model="editCategoryForm"
             @submit.native.prevent="save"
             :rules="editRule"
             label-width="80px">
      <el-form-item label="上级分类">
        <el-select v-model="editCategoryForm.parent"
                   placeholder="">
          <el-option v-for="item in parents"
                     :key="item._id"
                     :label="item.name"
                     :value="item._id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="名称"
                    prop="name">
        <el-row>
          <el-col :span="5">
            <el-input v-model="editCategoryForm.name"></el-input>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary"
                   native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: String
  },
  created () {
    this.id && this.getCategory()
    this.getParents()
  },
  data () {
    return {
      editCategoryForm: {},
      editRule: {
        name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' },
          { min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur' }
        ]
      },
      parents: []
    }
  },
  methods: {
    save () {
      this.$refs.editForm.validate(async val => {
        if (!val) {
          this.$message.error('请正确填写分类名')
        } else {
          if (this.id) {
            await this.$http.put(
              `/rest/categories/${this.id}`,
              this.editCategoryForm
            )
          } else {
            await this.$http.post('/rest/categories', this.editCategoryForm)
          }
          this.$message.success('保存成功')
          this.$router.push('/categories/categoryList')
        }
      })
    },
    async getCategory () {
      const res = await this.$http.get(`/rest/categories/${this.id}`)
      this.editCategoryForm = res.data
    },
    async getParents () {
      const res = await this.$http.get('/rest/categories')
      this.parents = res.data.items
      console.log(this.parents)
    }
  }
}
</script>

