<template>
  <div>
    <h1>{{ id ? "编辑" : "添加" }}广告位</h1>
    <el-form ref="editForm"
             :model="model"
             @submit.native.prevent="save"
             :rules="editRule"
             label-width="80px">
      <el-form-item label="广告名">
        <el-row>
          <el-col :span="10">
            <el-input v-model="model.name"></el-input>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item label="广告内容">
        <el-button type="text"
                   @click="model.items.push({})">+添加广告</el-button>
        <el-row type="flex"
                style="flex-wrap:wrap;justify-content:space-between">
          <el-col v-for="(item,index) in model.items"
                  :key="index"
                  :span="11">
            <el-form-item label="跳转链接"
                          style="margin:1rem 0">
              <el-input v-model="item.url"></el-input>
            </el-form-item>
            <el-form-item label="广告图片">
              <el-upload class="avatar-uploader"
                         :action="uploadUrl"
                         :show-file-list="false"
                         :headers="getAuthorization()"
                         :on-success="res=> $set(item,'image',res.url)">
                <img v-if="item.image"
                     :src="item.image"
                     class="avatar">
                <i v-else
                   class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="广告删除">
              <el-button type="danger"
                         @click="model.items.splice(index,1)">删除该广告</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary"
                   native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: String
  },
  created () {
    this.id && this.getAds()
  },
  data () {
    return {
      model: {
        items: []
      },
      editRule: {
        title: [
          { required: true, message: '请输入文章名称', trigger: 'blur' },
          { min: 2, max: 30, message: '长度在 2 到 30 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    save () {
      this.$refs.editForm.validate(async val => {
        if (!val) {
          this.$message.error('请正确填写分类名')
        } else {
          if (this.id) {
            await this.$http.put(
              `/rest/ads/${this.id}`,
              this.model
            )
          } else {
            await this.$http.post('/rest/ads', this.model)
          }
          this.$message.success('保存成功')
          this.$router.push('/ads/list')
        }
      })
    },
    async getAds () {
      const res = await this.$http.get(`/rest/ads/${this.id}`)
      this.model = res.data
    }
  }
}
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 5rem;
  height: 5rem;
  line-height: 5rem;
  text-align: center;
}
.avatar {
  width: 5rem;
  height: 5rem;
  display: block;
}
.el-icon-plus:before {
  content: '\e6d9';
  line-height: 5rem;
}
</style>
