<template>
  <div>
    <h1>物品列表</h1>
    <el-table :data="itemList"
              stripe
              border>
      <el-table-column prop="_id"
                       label="ID">
      </el-table-column>
      <el-table-column prop="name"
                       label="物品">
      </el-table-column>
      <el-table-column label="图标">
        <template slot-scope="scope">
          <img :src="scope.row.icon"
               style="height:3rem">
        </template>
      </el-table-column>
      <el-table-column label="其他操作">
        <template slot-scope="scope">
          <el-button type="primary"
                     icon="el-icon-edit"
                     @click="$router.push(`/items/edit/${scope.row._id}`)">编辑</el-button>
          <el-button type="danger"
                     icon="el-icon-delete"
                     @click="deleteItem(scope.row._id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="pagination.currentPage"
                   :page-sizes="[5, 10, 15, 20]"
                   :page-size="pagination.pageSize"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="count"
                   background>
    </el-pagination>
  </div>
</template>

<script>
export default {
  created () {
    this.getItemList()
  },
  data () {
    return {
      itemList: [],
      pagination: {
        currentPage: 1,
        pageSize: 5
      },
      count: 0
    }
  },
  methods: {
    async getItemList () {
      const res = await this.$http.get('/rest/items', { params: this.pagination })
      console.log(res)
      this.itemList = res.data.items
      this.count = res.data.count
    },
    deleteItem (id) {
      this.$confirm('此操作将永久删除该物品, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        await this.$http.delete(`/rest/items/${id}`)
        this.getItemList()
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
      })
    },
    handleSizeChange (val) {
      this.pagination.pageSize = val
      this.getItemList()
    },
    handleCurrentChange (val) {
      this.pagination.currentPage = val
      this.getItemList()
    }
  }
}
</script>

