<template>
  <div>
    <h1>{{ id ? '编辑' : '添加' }}英雄</h1>
    <el-form ref="editForm"
             :model="model"
             @submit.native.prevent="save"
             :rules="editRule"
             label-width="80px">
      <el-tabs type="border-card">
        <el-tab-pane label="基本信息">
          <el-form-item label="名称"
                        prop="name">
            <el-row>
              <el-col :span="5">
                <el-input v-model="model.name"></el-input>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="称号"
                        prop="title">
            <el-row>
              <el-col :span="5">
                <el-input v-model="model.title"></el-input>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="头像">
            <el-row>
              <el-col :span="5">
                <el-upload class="avatar-uploader"
                           :action="uploadUrl"
                           :show-file-list="false"
                           :headers="getAuthorization()"
                           :on-success="res => $set(model, 'avatar', res.url)">
                  <img v-if="model.avatar"
                       :src="model.avatar"
                       class="avatar" />
                  <i v-else
                     class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="背景图片">
            <el-row>
              <el-col :span="5">
                <el-upload class="avatar-uploader"
                           :action="uploadUrl"
                           :show-file-list="false"
                           :headers="getAuthorization()"
                           :on-success="res => $set(model, 'banner', res.url)">
                  <img v-if="model.banner"
                       :src="model.banner"
                       style="width:20rem;heigth:20rem;display:block" />
                  <i v-else
                     class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="类型">
            <el-row>
              <el-col :span="5">
                <el-select v-model="model.categories"
                           multiple>
                  <el-option v-for="item in categories"
                             :key="item._id"
                             :label="item.name"
                             :value="item._id"></el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="难度">
            <el-rate :max="9"
                     show-score
                     style="margin-top:0.6rem"
                     v-model="model.scores.difficult"></el-rate>
          </el-form-item>
          <el-form-item label="技能">
            <el-rate :max="9"
                     show-score
                     style="margin-top:0.6rem"
                     v-model="model.scores.skill"></el-rate>
          </el-form-item>
          <el-form-item label="攻击">
            <el-rate :max="9"
                     show-score
                     style="margin-top:0.6rem"
                     v-model="model.scores.attack"></el-rate>
          </el-form-item>
          <el-form-item label="生存">
            <el-rate :max="9"
                     show-score
                     style="margin-top:0.6rem"
                     v-model="model.scores.survive"></el-rate>
          </el-form-item>
          <el-form-item label="顺风出装">
            <el-row>
              <el-col :span="8">
                <el-select v-model="model.items1"
                           multiple>
                  <el-option v-for="item in items"
                             :key="item._id"
                             :label="item.name"
                             :value="item._id"></el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="逆风出装">
            <el-row>
              <el-col :span="8">
                <el-select v-model="model.items2"
                           multiple>
                  <el-option v-for="item in items"
                             :key="item._id"
                             :label="item.name"
                             :value="item._id"></el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="使用技巧">
            <el-row>
              <el-col :span="9">
                <el-input v-model="model.usageTips"
                          placeholder="使用技巧"
                          type="textarea"></el-input>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="对抗技巧">
            <el-row>
              <el-col :span="9">
                <el-input v-model="model.battleTips"
                          placeholder="对抗技巧"
                          type="textarea"></el-input>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item label="团战思路">
            <el-row>
              <el-col :span="9">
                <el-input v-model="model.teamTips"
                          placeholder="团战思路"
                          type="textarea"></el-input>
              </el-col>
            </el-row>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="技能">
          <el-button type="text"
                     @click="model.skills.push({})">+添加技能</el-button>
          <el-row type="flex"
                  style="flex-wrap:wrap;justify-content:space-between">
            <el-col v-for="(item, index) in model.skills"
                    :key="index"
                    :span="11">
              <el-form-item label="技能名称">
                <el-input v-model="item.skillName"></el-input>
              </el-form-item>
              <el-form-item label="技能冷却">
                <el-input v-model="item.delay"></el-input>
              </el-form-item>
              <el-form-item label="技能消耗">
                <el-input v-model="item.cost"></el-input>
              </el-form-item>
              <el-form-item label="技能图标">
                <el-upload class="avatar-uploader"
                           :action="uploadUrl"
                           :show-file-list="false"
                           :headers="getAuthorization()"
                           :on-success="res => $set(item, 'icon', res.url)">
                  <img v-if="item.icon"
                       :src="item.icon"
                       class="banner" />
                  <i v-else
                     class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="技能描述">
                <el-input v-model="item.description"
                          type="textarea"
                          :rows="4"></el-input>
              </el-form-item>
              <el-form-item label="技能删除">
                <el-button type="danger"
                           @click="model.skills.splice(index, 1)">删除该技能</el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="最佳搭档">
          <el-button type="text"
                     @click="model.partners.push({})">+添加英雄</el-button>
          <el-row type="flex"
                  style="flex-wrap:wrap;justify-content:space-between">
            <el-col v-for="(item, index) in model.partners"
                    :key="index"
                    :span="11">
              <el-form-item label="英雄名称">
                <el-select filterable
                           v-model="item.hero">
                  <el-option v-for="hero in allHeroes"
                             :key="hero._id"
                             :value="hero._id"
                             :label="hero.name"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="英雄描述">
                <el-input v-model="item.description"
                          type="textarea"></el-input>
              </el-form-item>
              <el-form-item label="关系删除">
                <el-button type="danger"
                           @click="model.partners.splice(index, 1)">删除该搭档</el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
      </el-tabs>
      <el-form-item style="margin-top:1rem;margin-left:-70px">
        <el-button type="primary"
                   native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: String
  },
  created () {
    this.id && this.getheroes()
    this.getCategories()
    this.getItems()
    this.getAllHeroes()
  },
  data () {
    return {
      model: {
        name: '',
        avatar: '',
        banner: '',
        title: '',
        categories: [],
        scores: {
          difficult: 0,
          skill: 0,
          attack: 0,
          survive: 0
        },
        skills: [],
        partners: []
      },
      editRule: {
        name: [{
          required: true,
          message: '请输入英雄名称',
          trigger: 'blur'
        }, {
          min: 2,
          max: 8,
          message: '长度在 2 到 8 个字符',
          trigger: 'blur'
        }]
      },
      categories: [],
      items: [],
      allHeroes: []
    }
  },
  methods: {
    save () {
      this.$refs.editForm.validate(async val => {
        if (!val) {
          this.$message.error('请正确填写分类名')
        } else {
          if (this.id) {
            await this.$http.put(`/rest/heroes/${this.id}`, this.model)
          } else {
            await this.$http.post('/rest/heroes', this.model)
          }
          this.$message.success('保存成功')
          this.$router.push('/heroes/list')
        }
      })
    },
    async getheroes () {
      const res = await this.$http.get(`/rest/heroes/${this.id}`)
      console.log(res)
      this.model = Object.assign({}, this.model, res.data)
    },
    async getCategories () {
      const res = await this.$http.get('/rest/categories')
      this.categories = res.data.items
    },
    async getItems () {
      const res = await this.$http.get('/rest/items')
      this.items = res.data.items
    },
    async getAllHeroes () {
      const res = await this.$http.get('/rest/heroes')
      console.log(res)
      this.allHeroes = res.data.items
    }
  }
}
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 5rem;
  height: 5rem;
  line-height: 5rem;
  text-align: center;
}

.avatar {
  width: 5rem;
  height: 5rem;
  display: block;
}

.el-icon-plus:before {
  content: '\e6d9';
  line-height: 5rem;
}
</style>
